.main-wrapper{
    height: 100vh;
    width:100vw;
    display: grid;
    align-items: center;
}
.main{
    height: 500px;
    display: grid;
    grid-template-columns: 1fr 1fr;
}
form{
    display: flex;
    flex-direction: column;
    width: 300px;
    margin: 0 auto;
}
input{
    margin-bottom: 20px ;
}
tr{
    padding: 20px;
    border-bottom: 1px solid black;
}
th td{
    padding: 20px 0px;
}
table{
    height: 300px;
}